.box
{
  width: 100px;
  height: 100px;
  background-color: #888;
  border-radius: 4px;
  border: 1px solid #000;
  margin: 5px;
  cursor: pointer;
  -webkit-transition: ease .3s;
  -o-transition: ease .3s;
  transition: ease .3s;
}
@media screen and (max-width: 767px)
{
  .box
  {
    width: 50px;
    height: 50px;
  }
}



