.finished
{
  margin-top: 40px;
  opacity: 0;
  -webkit-transform:translateY(15px);
      -ms-transform:translateY(15px);
          transform:translateY(15px);
  -webkit-animation: fadeIn ease .6s forwards;
          animation: fadeIn ease .6s forwards;
}
.finished-title
{
  font-size: 28px;
  text-align: center;
}
.retry
{
  width: 150px;
  height: 50px;
  margin: 0 auto;
  margin-top: 40px;
  border: 1px solid #000;
  border-radius: 4px;
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-transform: uppercase;
  cursor: pointer;
}

@-webkit-keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media screen and (max-width: 767px)
{
  .finished-title
  {
    font-size: 24px;
  }
  .retry
  {
    font-size: 14px;
    margin-top: 30px;
  }
}



