.boxes-list
{
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
  padding: 0 40px;
}
@media screen and (max-width: 991px)
{
  .boxes-list
  {
    max-width: 620px;
  }
}
@media screen and (max-width: 767px)
{
  .boxes-list
  {
    max-width: 320px;
  }
}



